import React, { Fragment, useState } from 'react';
import { connect } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';

import format from 'date-fns/format';
import addDays from 'date-fns/add_days';
import subDays from 'date-fns/sub_days';

import ReactCountryFlag from 'react-country-flag';
import FriendChip from './FriendChip.react';
import DatePicker from './DatePicker.react';
import { updateTrip, deleteTrip } from '../actions';

function TripCard({
  updateTrip,
  deleteTrip,
  disabledDates,
  viewOnly,
  isHighlighted,
  ...trip
}) {
  const { id, city, start_date, end_date, matches } = trip;

  const [promptDel, setPromptDel] = useState(false);
  const [dates, setDates] = useState([
    new Date(start_date),
    new Date(end_date)
  ]);

  const updateDates = d => {
    setDates(d);
    if (d.length !== 2) {
      return;
    }
    return updateTrip({
      id,
      city: city.id,
      start_date: format(d[0], 'YYYY-MM-DD'),
      end_date: format(d[1], 'YYYY-MM-DD')
    });
  };

  const disabledDatesMinusThoseForThisTrip = (disabledDates || []).filter(
    ({ from, to }) =>
      !(
        format(addDays(start_date, 1), 'YYYY-MM-DD') ===
          format(from, 'YYYY-MM-DD') &&
        format(subDays(end_date, 1), 'YYYY-MM-DD') === format(to, 'YYYY-MM-DD')
      )
  );

  const cls = `uk-tile uk-padding-small uk-flex uk-flex-between uk-link-muted ${
    isHighlighted ? 'background-glow' : ''
  }`;

  if (promptDel) {
    return (
      <Fragment>
        <div className={cls}>
          <div>
            <h6>
              <ReactCountryFlag code={city.country_code} svg /> | {city.name}
            </h6>
          </div>
          <p>Confirm you would like to delete this trip</p>
          <span className="uk-flex">
            <hr className="uk-divider-vertical uk-margin-right uk-margin-left" />
            <span className="uk-flex uk-flex-column">
              <span className="icon-sm">
                <span
                  className="icono-check"
                  onClick={() => deleteTrip({ id })}
                />
              </span>
              <span className="icon-sm">
                <span
                  className="icono-cross"
                  onClick={e => setPromptDel(false)}
                />
              </span>
            </span>
          </span>
        </div>
        <hr />
      </Fragment>
    );
  }

  const WrapperComponent = viewOnly ? 'div' : Link;

  return (
    <Fragment>
      <div className={cls}>
        <div>
          <WrapperComponent to={`/trips/${id}`}>
            <h6>
              <ReactCountryFlag code={city.country_code} svg /> | {city.name}
            </h6>
          </WrapperComponent>
          <DatePicker
            disabledDates={disabledDatesMinusThoseForThisTrip}
            value={dates}
            onChange={updateDates}
            disabled={viewOnly}
          />
        </div>
        <div className="uk-flex">
          <span className="uk-flex">
            {matches.slice(0, 3).map(m => (
              <FriendChip key={m.id} {...m} />
            ))}
            <ExtraFriends allFriends={matches} numDisplayed={3} />
          </span>
          {!viewOnly && (
            <span className="uk-flex">
              <hr className="uk-divider-vertical uk-margin-right uk-margin-left" />
              <span className="uk-flex uk-flex-column">
                <span className="icon-sm">
                  <span
                    className="icono-trash"
                    onClick={e => {
                      e.preventDefault();
                      setPromptDel(true);
                    }}
                  />
                </span>
              </span>
            </span>
          )}
        </div>
      </div>
      <hr />
    </Fragment>
  );
}

const ExtraFriends = ({ allFriends, numDisplayed }) => {
  if (allFriends.length > numDisplayed) {
    return (
      <div className="uk-border-circle avatar-preview uk-background-muted">
        <p className="uk-border-circle uk-dark">
          +{allFriends.length - numDisplayed}
        </p>
      </div>
    );
  }
  return null;
};

const mapStateToProps = () => ({});

export default connect(
  mapStateToProps,
  {
    updateTrip,
    deleteTrip
  }
)(withRouter(TripCard));
