import React, { useEffect, useState, Fragment } from 'react';
import { connect } from 'react-redux';

import differenceInDays from 'date-fns/format';
import { determineAlreadySelectedTripDates } from '../helpers/dateUtils';
import {
  fetchUser,
  fetchTrips,
  requestFriendship,
  acceptFriendRequest
} from '../actions';

import Spinner from '../components/Spinner.react';
import NewTrip from '../components/NewTrip.react';
import TripCard from '../components/TripCard.react';
import FriendStatusButton from '../components/FriendStatusButton.react';
import LocationMap from '../components/LocationMap.react';
import TelegramLink from '../components/TelegramLink.react';

const UserProfile = ({
  match,
  users,
  fetchUser,
  fetchTrips,
  trips,
  cities,
  activeUser,
  requestFriendship,
  acceptFriendRequest
}) => {
  const userId = parseInt(match.params.id, 10);
  const [highlightedTrip, setActiveLocation] = useState(null);

  // Clone the trip
  let user = users.all[userId];

  useEffect(
    () => {
      // Get it from the API if:
      //   - it is not currently fetching users
      if (!users.isFetching && userId !== activeUser) {
        fetchUser(userId);
      }
      fetchTrips({ id: userId });
    },
    [userId]
  );

  if (users.isFetching || !user) {
    return <Spinner />;
  }

  const populatedTrips = trips.ids
    .map(tId => {
      const trip = trips.all[tId];
      return {
        ...trip,
        city: cities.all[trip.city],
        matches: trip.matches.map(match => {
          const [uId, start, end] = match.split('::');
          return {
            ...users.all[uId],
            start_date: start,
            end_date: end
          };
        })
      };
    })
    .filter(t => t.user === userId);

  // Sort so the soonest trip is first in the list
  populatedTrips.sort((a, b) => differenceInDays(a.start_date, b.start_date));

  const onRequest = () => requestFriendship(user);
  const onAccept = () => acceptFriendRequest(user);
  const disabledDates = determineAlreadySelectedTripDates(populatedTrips);
  return (
    <div>
      <div className="centered-hero">
        <div className="uk-flex uk-flex-left profile-details">
          <div className="uk-flex uk-flex-left">
            <img
              className="avatar-big uk-border-circle"
              alt="avatar"
              src={user.photo_url || '/blank-profile-picture.png'}
            />

            <div className="uk-flex uk-flex-column uk-padding">
              {user.username && <h4>@{user.username}</h4>}
              <span>
                {user.first_name.toUpperCase()} {user.last_name.toUpperCase()}
              </span>
            </div>
          </div>

          <div className="user-icons">
            <FriendStatusButton
              user={user}
              onRequest={onRequest}
              onAccept={onAccept}
            />
          </div>
          <div className="user-icons">
            <TelegramLink username={user.username} />
          </div>
        </div>

        <hr />

        <LocationMap
          userId={user.id}
          locations={populatedTrips}
          onSelect={setActiveLocation}
        />
      </div>

      {user.id === userId && (
        <Fragment>
          <hr />
          <h4>ADD NEW TRIP</h4>
          <NewTrip trips={populatedTrips} />
        </Fragment>
      )}

      <hr />

      {populatedTrips.map(trip => (
        <TripCard
          key={trip.id}
          disabledDates={disabledDates}
          {...trip}
          isHighlighted={trip.id === highlightedTrip}
          viewOnly={user.id !== userId}
        />
      ))}
    </div>
  );
};

const mapStateToProps = ({ cities, trips, users, activeUser }) => ({
  users,
  trips,
  cities,
  activeUser
});

export default connect(
  mapStateToProps,
  {
    fetchUser,
    fetchTrips,
    requestFriendship,
    acceptFriendRequest
  }
)(UserProfile);
